.emotionsAbout {
    margin-top: 44px;
}

.mainImage {
    max-height: 546px;
    max-width: 740px;
    border-radius: 8px;
}

.image {
    height: 170px;
    width: 169px;
    border-radius: 8px;
    margin: 0 0 20px 20px;
}

.images {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.emotionsAboutImages {
    display: flex;
    flex-direction: row;
}

.frame {
    height: 170px;
    width: 169px;
    border-radius: 8px;
    margin: 0 0 20px 20px;
    background-color: #0468FF;
    color: #FFFFFF;
}

.frame {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.frame_icon {
    width: 25px;
    height: 25px;
    background-size: 25px 25px;
    background-image: url("data:image/svg+xml;base64, ");
}

.emotionsAboutTitle {
    margin: 43.53px 0 24.53px 0;
    font-size: 24px;
}

.emotionsAboutGrid {
    font-family: 'Roboto';
    font-size: 14px;
    line-height: 2;
    width: 100%;
}