.EmotionsTopRowContainer {
    box-shadow: 0 6px 4px -4px rgba(0, 0, 0, .2);
    overflow: hidden;
    background-color: #FFFFFF;
}

.EmotionsTopRow {
    max-width: 1120px;
    margin: 0 auto 0 auto;
}

.EmotionsBreadcrumps {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 44px;
    font-family: "Roboto";
    color: #262626;
}

.EmotionsBreadcrumpsLeft {
    display: flex;
    flex-direction: row;
}

.arrow-right {
    width: 20px;
    height: 20px;
    background-size: 20px 20px;
    background-image: url("data:image/svg+xml;base64, ");
    background-repeat: no-repeat;
}

.EmotionsBreadcrumpsRight {
    display: flex;
    flex-direction: row;
}

.item-favorites {
    display: flex;
    flex-direction: row;
}

.item-share {
    display: flex;
    flex-direction: row;
    padding-right: 12px;
}

.share_top {
    width: 20px;
    height: 20px;
    background-size: 20px 20px;
    background-image: url("data:image/svg+xml;base64, ");
    background-repeat: no-repeat;
}

.favorites {
    width: 24px;
    height: 24px;
    background-size: 15px 20px;
    background-image: url("data:image/svg+xml;base64, ");
    background-repeat: no-repeat;
    background-position: center center;
}

.title_header {
    margin-top: 64px;
    font-family: "Montserrat";
    font-size: 32px;
}

.EmotionsTabs {
    display: flex;
    flex-direction: row;
    margin-top: 44px;
    justify-content: center;
    align-items: center;
    padding-bottom: 24px;
}

.RoutsRibbonButtons {
    display: flex;
    flex-direction: row;
    margin-right: 12px;
    align-items: center;
    text-align: center;
    font-family: "Roboto";
    font-size: 16px;
    color: #262626;
}

.icon-overview {
    width: 32px;
    height: 32px;
    background-size: 32px 32px;
    background-image: url("data:image/svg+xml;base64, ");
    background-repeat: no-repeat;
}


.icon-organizer {
    width: 24px;
    height: 24px;
    background-size: 24px 24px;
    background-image: url("data:image/svg+xml;base64, ");
    background-repeat: no-repeat;
}

.icon-information {
    width: 32px;
    height: 32px;
    background-size: 32px 32px;
    background-image: url("data:image/svg+xml;base64, ");
    background-repeat: no-repeat;
}

.icon-date {
    width: 32px;
    height: 32px;
    background-size: 32px 32px;
    background-image: url("data:image/svg+xml;base64, ");
    background-repeat: no-repeat;
}

.title-organizer,
.title-information,
.title-date,
.title-overview {
    margin-left: 8px;
}

.EmotionTabs_fix {
    position: fixed;
    max-width: 1280px;
    width: 100%;
    margin: -17.4rem 8rem 0 -5rem;
    z-index: 5;
    background-color: #f0f0f0;
    padding-top: 10px;
}

.EmotionTabs_fix_plug {
    width: 75%;
    height: 98px;
}


@media screen and (max-width: 1280px){
    .EmotionTabs_fix {
        width: 100%
    }
}

@media screen and (max-width: 1200px){
    .EmotionsTopRow {
        max-width: 1120px;
        margin: 0 5rem 0 5rem;
    }
    .EmotionsTopRowContainer {
        width: 100%;
    }
    .EmotionTabs_fix {
        width: 110%
    }
}

@media screen and (max-width: 1080px){
    .EmotionTabs_fix {
        width: 100%
    }
}


@media screen and (max-width: 990px){
    .EmotionsTopRowContainer {
        width: 100%;
    }
    .EmotionsTopRow {
        max-width: 1120px;
        margin: 0 5rem 0 5rem;
    }
}

@media screen and (max-width: 475px){
    .EmotionTabs_fix {
        margin-top: -19rem
    }
}